// welcome page
.auth-header {
    border-bottom: 1px solid #d2d2d2;
}
.forgot-password.auth-page {
    padding: 200px 0px !important;
}
.reset-pwd.auth-center.auth-wrapper {
    max-width: 100% !important;
}
.login.auth-page {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
}
.auth-wrapper {
    background-color: $white;
    box-shadow: 7px 31px 81px 0px #00000014;
    border-radius: 14px;
    padding: 27px 49px 31px 38px;
    max-width: 600px;
    margin: auto !important;
    min-height: 380px;
    .form-title{
        margin-bottom: 30px;
    }
    .row {
        display: flex;
        justify-content: space-between;
        // align-items: flex-start;

        .auth-image {
            min-height: 600px;
            background: #f8f8f8;
            border-radius: 5px;
            display: flex;
            justify-content: center;
            align-items: center;
            max-width: 50%;
        }
        .comman-form-col {
            margin-left: 37px;
            max-height: 710px;
            overflow: auto;
            overflow-x: hidden;
            &::-webkit-scrollbar {
                width: 0;
                height: 0;
            }
            .form-title {
                margin-bottom: 20px;
                h2 {
                    font-size: 30px;
                    line-height: 36px;
                    color: $black;
                }
                p {
                    font-size: 18px;
                    font-weight: 500;
                    line-height: 32px;
                    color: #666666;
                }
            }
            form {
                .input-wrapper {
                    position: relative;
                    margin-bottom: 22px !important;
                    .label-name {
                        font-size: 14px;
                        font-weight: 500;
                        line-height: 26px;
                        color: #666666;
                        position: relative;
                    }
                }
                .input-text {
                    font-size: 16px;
                    font-weight: 500;
                    color: $black;
                    height: 50px;
                    resize: none;
                    padding: 0px 10px;
                    &:focus,
                    &:active {
                        box-shadow: none;
                        background-color: #f8f8f8;
                    }
                    &:-webkit-autofill,
                    &:-webkit-autofill:hover,
                    &:-webkit-autofill:focus,
                    &:-webkit-autofill:active {
                        -webkit-box-shadow: 0 0 0 30px #f8f8f8 inset !important;
                    }
                }

                .pwd-input {
                    position: relative;
                }
                .forgot-btn {
                    text-decoration: none;
                    font-weight: 400;
                    font-size: 14px;
                    line-height: 26px;
                    color: $black;
                    cursor: pointer;
                    &:hover {
                        color: #666666;
                    }
                }
                .comman-auth-btn {
                    width: 100% !important;
                    height: 50px !important ;
                    &:active {
                        background-color: #814b99;
                        border: #814b99;
                    }
                }
                .btn:disabled {
                    background-color: #814b99;
                    border: #814b99;
                    opacity: 0.6;
                    &:hover {
                        color: $white;
                    }
                }
                .input-group-text {
                    display: flex;
                    align-items: center;
                    padding: 0 20px;
                    background-color: #f8f8f8;
                    border: none;
                    cursor: pointer;
                    border: 1px solid #f5f5f5;
                    border-left: none;
                    z-index: 5;
                }
            }
        }
        .or-divider {
            position: relative;
            padding: 15px 0;
            display: flex;
            align-items: center;
            justify-content: center;
            width: 100%;
            hr {
                width: 100%;
            }
            span {
                position: absolute;
                background: white;
                padding: 0 15px;
                color: #666666;
            }
        }
        .login-with-google {
            .google-btn {
                width: 100%;
                background: #f9f9f9;
                border: 1px solid #ededed;
                border-radius: 5px;
                height: 50px;
                font-weight: 500;
                font-size: 14px;
                line-height: 17px;
                color: $black;

                &:hover {
                    background-color: $primary;
                    color: $white;
                    border: $primary;
                }
                span {
                    margin-left: 14px;
                }
            }
        }
        .joinus-box {
            background: #f9f9f9;
            border: 1px solid #f2f4f5;
            border-radius: 5px;
            padding: 24px 10px 27px;
            text-align: center;
            width: 100%;
            margin-top: 24px;
            h4 {
                font-weight: 500;
                font-size: 20px;
                line-height: 24px;
                text-align: center;
                color: $black;
            }
            p {
                font-weight: 400;
                font-size: 14px;
                line-height: 17px;
                color: #666666;
                text-align: center;
                margin: 11px 0 27px;
            }
            .joinus-btn {
                width: 266px !important;
                height: 50px !important;
                background: rgba(0, 42, 58, 0.1);
                border-radius: 5px;
                border: none;
                font-weight: 600;
                font-size: 16px;
                line-height: 19px;
                margin-top: 24px !important;
                color: $primary;
                &:hover {
                    background-color: $primary;
                    color: $white;
                }
            }
        }
    }
}
// Forgot Password page
.auth-center {
    .previous-page-icon {
        width: 130px;
        text-align: left;
        span {
            font-weight: 400;
            display: flex;
            align-items: center;
            cursor: pointer;
            &:hover {
                color: #666666;
            }
            .back-icon {
                margin-right: 15px;
            }
        }
    }
    .for-mobile-back-btn {
        display: none;
    }
    .row {
        .form-wrapper {
            display: flex;
            flex-direction: column;

            .center-form {
                max-width: 380px;
                margin: 26px auto 0;
                .form-title {
                    h2 {
                        text-align: center;
                    }
                    p {
                        margin-top: 13px;
                        text-align: center;
                        font-size: 14px;
                        line-height: 20px;
                    }
                }
                .reset-btn {
                    margin-top: 26px;
                }
            }
        }
    }
}
//Reset password page css
.reset-password {
    .reset-pwd {
        padding: 0;
        height: 770px;
        .reset-page-header {
            padding: 20px 0px 10px 30px !important;
            display: flex;
            justify-content: flex-start;
            align-items: center;
            border-bottom: 1px solid #dcdcdc;
        }
        .reset-form {
            margin: 43px auto 26px !important;
            .reset-pwd-btn {
                margin-top: 15px;
            }
        }
        .reset-pwd-form {
            form {
                margin-top: 30px;
                .input-wrapper {
                    margin-bottom: 25px;
                }
            }
        }
    }
}
// Create new account page css
.auth-wrapper {
    .row {
        .create-account-form {
            .form-title {
                margin: 28px 0 34px;
            }
            form {
                .privacy-text {
                    display: flex;
                    align-items: flex-start;
                    margin: 28px 0 25px;
                    p {
                        font-size: 13px;
                        line-height: 20px;
                        color: #444444;
                        font-weight: 400;
                        span {
                            font-size: 13px;
                            line-height: 20px;
                            color: #0878ff;
                            text-decoration: underline;
                            font-weight: 500;
                        }
                    }
                }

                .joinus-box {
                    padding: 29px 15px 36px;
                    h4 {
                        font-size: 18px;
                    }
                }
            }
        }
    }
}
// company-profile page css
.auth-wrapper {
    .row {
        .company-details-form {
            .form-title {
                margin-bottom: 10px;
            }
            form {
                .cd-submit-btn {
                    margin-top: 30px;
                }
            }
        }
    }
}
//  verification form page css
.auth-wrapper {
    .row {
        .form-wrapper {
            .verification-form {
                max-width: 560px;
                .form-title {
                    p {
                        margin-top: 18px;
                        b {
                            font-weight: 700;
                            color: #666666;
                        }
                    }
                }
                form {
                    max-width: 400px;
                    margin: 0 auto;

                    .input-wrapper {
                        margin-top: 49px;
                        margin-bottom: 32px;
                        .label-name {
                            margin-bottom: 30px;
                        }
                        .otp-wrapper {
                            display: flex;
                            flex-wrap: wrap;
                            gap: 20px;
                        }
                        .input-text {
                            max-width: 50px;
                            display: inline-block;
                            // margin-right: 20px;
                            font-size: 22px;
                            -webkit-appearance: none;
                            appearance: none;
                            text-align: center;
                            &:last-child {
                                margin-right: 0;
                            }
                            &::-webkit-outer-spin-button,
                            &::-webkit-inner-spin-button {
                                -webkit-appearance: none;
                                margin: 0;
                            }
                        }
                    }
                    .resend-otp {
                        color: #458cf8;
                        font-size: 14px;
                        font-weight: 400;
                        text-decoration: underline;
                        line-height: 17px;
                        margin-top: 22px;
                        cursor: pointer;
                    }
                }
            }
        }
    }
}
// Number verification page css
.auth-wrapper {
    .row {
        .number-verification-form {
            .verification-form {
                form {
                    .wrong-num {
                        color: #666666;
                        font-size: 14px;
                        font-weight: 400;
                        text-decoration: underline;
                        line-height: 17px;
                        margin-top: 22px;
                        cursor: pointer;
                    }
                }
            }
        }
    }
}
//  complete profile page

.auth-wrapper {
    .row {
        .complete-profile-form {
            max-height: 850px;
            overflow: auto;
            overflow-x: hidden;

            .form-title {
                margin-top: 30px;
            }
            position: relative;
            .skip-box {
                position: absolute;
                top: 0px;
                right: 0px;
                background: #f8f8f8;
                border-radius: 0px 0px 0px 22px;
                display: flex;
                align-items: center;
                padding: 16px 25px 18px 22px;
                p {
                    font-size: 16px;
                    line-height: 26px;
                }
                span {
                    text-decoration: underline;
                    margin-left: 5px;
                    cursor: pointer;
                    &:hover {
                        color: #666666;
                    }
                }
            }
            .user-profile-form {
                .row {
                    .input-wrapper {
                        position: relative;
                        margin-top: 23px;

                        &:first-child {
                            margin-top: 17px;
                        }
                        .down-arrow {
                            z-index: 10;
                            position: absolute;
                            right: 22px;
                            top: 55px;
                        }
                        .form-select {
                            cursor: pointer;
                        }
                        .text-area {
                            resize: none;
                            &::placeholder {
                                color: rgba(0, 0, 0, 0.6);
                                font-size: 14px;
                            }
                        }
                    }
                }
                .work-title {
                    margin: 56px 0 28px;
                    h3 {
                        font-size: 16px;
                        line-height: 26px;
                    }
                }
                .experience-box {
                    display: flex;
                    align-items: center;
                    background: #f1f4f1;
                    padding: 18px 25px 21px;
                    border-radius: 34px 0px;
                    gap: 20px;
                    position: relative;
                    cursor: pointer;
                    &.active {
                        border: 1px solid #814b99;
                        .check-icon {
                            display: block;
                            position: absolute;
                            right: -12px;
                            top: -12px;
                        }
                    }
                    .text {
                        border-left: 1px solid #dadada;
                        padding-left: 22px;
                        h5 {
                            font-size: 16px;
                            line-height: 26px;
                            color: #000000;
                            font-weight: 600;
                        }
                        p {
                            font-size: 14px;
                            line-height: 19px;
                            margin-top: 3px;
                        }
                    }
                    .check-icon {
                        display: none;
                    }
                }
                .internship {
                    margin-top: 24px;
                }
                .upload-cv {
                    margin: 54px 0 0;
                    h5 {
                        font-size: 16px;
                        line-height: 26px;
                        font-weight: 600;
                        margin-bottom: 9px;
                    }
                    .input-file {
                        margin-top: 30px;
                        border: 1px solid rgba(0, 42, 58, 0.18);
                        border-radius: 5px;
                        padding: 7px 8px;
                        display: flex;
                        align-items: center;
                        column-gap: 13px;
                        cursor: pointer;
                        .upload-btn {
                            width: 186px;
                            height: 40px;
                            background: rgba(0, 42, 58, 0.05);
                            border-radius: 5px;
                            color: #814b99;
                            border: none;
                            display: flex;
                            padding: 0 0 0 20px;
                            align-items: center;
                            span {
                                font-size: 16px;
                                margin-left: 9px;
                            }
                        }
                        p {
                            font-size: 14px;
                            font-weight: 400;
                        }
                    }
                }
                .comman-content {
                    margin: 53px 0 0;
                    h5 {
                        font-size: 16px;
                        line-height: 26px;
                        font-weight: 600;
                        margin-bottom: 11px;
                    }
                    .box {
                        background-color: #f8f8f8;
                        border-radius: 5px;
                        height: 266px;
                        display: flex;
                        justify-content: center;
                        .content {
                            display: flex;
                            justify-content: center;
                            align-items: center;
                            flex-direction: column;
                            h6 {
                                font-size: 14px;
                                line-height: 20px;
                                color: #000000;
                                font-weight: 500;
                                margin-top: 18px;
                            }
                            p {
                                font-size: 14px;
                                line-height: 20px;
                                margin: 5px 0 18px;
                            }
                            .add-button {
                                background: rgba(0, 42, 58, 0.1);
                                border-radius: 5px;
                                font-size: 14px;
                                line-height: 20px;
                                color: #814b99;
                                border: none;
                                width: 265px;
                                height: 40px;
                                font-weight: 500;
                                &:hover {
                                    background-color: #814b99;
                                    color: #ffffff;
                                }
                            }
                        }
                    }
                }
                .skills {
                    margin-top: 22px;
                    .input-group {
                        margin-bottom: 20px;
                        .icontrol {
                            padding: 0;
                        }
                        .input-group-text {
                            padding: 0 14px 0 20px;
                        }
                        .form-control {
                            &::placeholder {
                                color: rgba(0, 0, 0, 0.6);
                                font-size: 16px;
                            }
                        }
                    }
                    .skills-box {
                        height: 182px;
                    }
                }
                .profile-submit-btn {
                    width: 137px !important;
                    margin-top: 40px;
                }
            }
        }

        ::-webkit-scrollbar {
            width: 5px;
        }
        ::-webkit-scrollbar-track {
            background: #f1f1f1;
        }
        ::-webkit-scrollbar-thumb {
            background: #eaeaea;
        }
    }
}

// privacy policy page
.comman-policy-page {
    padding-top: 60px;
    .page-title {
        background-color: #f1f4f1;
        min-height: 240px;
        h2 {
            font-size: 36px;
            line-height: 44px;
            text-align: center;
            padding: 65px 0 50px;
        }
    }
    .comman-condition {
        background: #ffffff;
        border: 1px solid rgba(176, 176, 176, 0.2);
        box-shadow: 7px 31px 81px rgba(0, 0, 0, 0.05);
        border-radius: 5px;
        padding: 50px 90px 50px 50px;
        position: relative;
        top: -75px;
        .policy {
            margin-top: 30px;
            h3 {
                font-size: 22px;
                font-weight: 500;
            }
            p {
                font-size: 16px;
                line-height: 26.5px;
                font-weight: 500;
                margin-top: 20px;
            }
            &:nth-child(1) {
                margin-top: 0;
            }
        }
    }
}
// about page css
.aboutus-page {
    .page-title {
        background: #f1f4f1;
        padding: 140px 0 58px;
        h1 {
            font-size: 36px;
            line-height: 44px;
            text-align: center;
        }
    }
    .aboutus {
        .introduce {
            margin: 138px 0 140px;
        }
        .our-mission {
            background-color: #f9f9f9;
            padding: 90px 0 100px;
            overflow-x: hidden;
            .our-mission-image {
                position: relative;
                display: inline-block;
                img {
                    z-index: 2;
                    position: relative;
                }
                &:before {
                    position: absolute;
                    content: "";
                    background-image: url("../images/dotted-design.svg");
                    top: -45px;
                    right: -30px;
                    width: 190px;
                    height: 190px;
                    background-size: contain;
                    background-repeat: no-repeat;
                    z-index: 0;
                }
            }
            .our-mission-col {
                position: relative;
                display: flex;
                justify-content: center;
                .hired-candidates {
                    position: absolute;
                    background: #ffffff;
                    box-shadow: -5px 5px 25px rgba(0, 0, 0, 0.05);
                    border-radius: 5px;
                    padding: 20px 30px;
                    max-width: 200px;
                    bottom: 40px;
                    z-index: 10;
                    left: -100px;
                    p {
                        color: #000000;
                        font-size: 16px;
                        line-height: 26px;
                        font-weight: 600;
                    }
                    ul {
                        display: flex;
                        margin-top: 7px;
                        li {
                            position: relative;

                            &:nth-child(1) {
                                left: 0px;
                            }
                            &:nth-child(2) {
                                left: -6px;
                            }
                            &:nth-child(3) {
                                left: -12px;
                            }
                            &:nth-child(4) {
                                left: -18px;
                            }
                            &:nth-child(5) {
                                left: -24px;
                            }
                        }
                    }
                }
            }
            .aboutus-content {
                max-width: 93%;
            }
        }
        .col-lg-6 {
            img {
                max-width: 100%;
            }
        }
        .aboutus-image-col {
            display: flex;
            justify-content: center;
            .about-image {
                position: relative;
                display: inline-block;
                &:before {
                    position: absolute;
                    content: "";
                    background-image: url("../images/dotted-design.svg");
                    top: -35px;
                    left: -36px;
                    width: 190px;
                    height: 190px;
                    background-size: contain;
                    background-repeat: no-repeat;
                    z-index: -1;
                }
            }
        }

        .aboutus-content {
            span {
                font-size: 18px;
                font-weight: 500;
                line-height: 73px;
                color: #949494;
            }
            h3 {
                color: $primary;
                line-height: 55px;
            }
            p {
                font-size: 16px;
                line-height: 26.5px;
                letter-spacing: 0.5px;
                color: #000000;
                margin-top: 20px;
            }
        }
        .our-workflow {
            margin: 156px 0 168px;
            .title {
                text-align: center;
                span {
                    font-size: 18px;
                    font-weight: 500;
                    line-height: 73px;
                    color: #949494;
                }
                h2 {
                    font-size: 30px;
                    line-height: 37px;
                    font-weight: 700;
                }
            }
            .our-workflow-content {
                margin-top: 82px;
                .row {
                    .col-lg-4 {
                        .content {
                            max-width: 386px;
                            margin: 0 auto;
                            text-align: center;
                            margin-top: 36px;
                            h3 {
                                font-size: 20px;
                                line-height: 24px;
                                font-weight: 700;
                            }
                            p {
                                font-size: 18px;
                                line-height: 29px;
                                font-weight: 500;
                                color: #a3a3a3;
                                margin-top: 18px;
                                text-align: center;
                            }
                        }
                    }
                }
            }
        }
        .search-job {
            margin-bottom: 150px;
            .search-job-box {
                background-image: url("../images/search-job-bg.png");
                background-size: cover;
                min-height: 300px;
                border-radius: 5px;
                display: flex;
                justify-content: center;
                align-items: center;
                flex-direction: column;
                position: relative;
                h4 {
                    color: $white;
                    font-size: 20px;
                    line-height: 24px;
                    font-weight: 700;
                    margin-bottom: 35px;
                }
                .search-job-btn {
                    border: 1px solid $white;
                    min-width: 146px;
                    &:hover {
                        background: $white;
                        border-radius: 5px;
                    }
                }
            }
        }
    }
}
// }
// page-not-found 404  css
.page-not-found {
    padding-top: 150px !important;
    padding-bottom: 150px;
    .error-404 {
        .error-content {
            h1 {
                color: #3d4852;
                font-size: 190px;
                font-weight: 900;
                letter-spacing: 15px;
                line-height: 190px;
            }
            p {
                text-align: center;
                font-size: 34px;
                font-weight: 600;
                margin: 20px 0 40px;
                color: #3d4852;
            }
            .pagenot-found-btn {
                margin: 0 auto;
                display: flex;
                justify-content: center;
                align-items: center;
                width: 230px;
                // text-transform: uppercase;
                letter-spacing: 0.5px;
                &:active {
                    background-color: $primary;
                    border: $primary;
                }
            }
        }
    }
}
// server page error
.server-error-page {
    padding-top: 150px !important;
    padding-bottom: 150px;
    .server-work {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        .server-image {
            img {
                max-width: 100%;
            }
        }
        .server-content {
            margin: 40px 0 20px;

            display: flex;
            justify-content: center;
            align-items: center;
            flex-direction: column;
            p {
                font-size: 30px;
                font-weight: 600;
                color: #3d4852;
                text-align: center;
                margin-bottom: 50px;
                max-width: 300px;
                line-height: 40px;
                span {
                    font-size: 30px;
                    font-weight: 800;
                }
            }
            .try-again-btn {
                margin: 0 auto;
                display: flex;
                justify-content: center;
                align-items: center;
                width: 200px;
                &:active {
                    background-color: $primary;
                    border: $primary;
                }
            }
        }
    }
}
// modal popup
.modal-dialog {
    .modal-content {
        padding: 34px 50px 40px;
        .modal-body {
            padding: 0;
        }
        .modal-header {
            padding: 0;
            padding-bottom: 26px;
            .close-btn {
                cursor: pointer;
            }
        }
        form {
            .input-wrapper {
                position: relative;
                margin-top: 33px;
                .down-arrow {
                    z-index: 10;
                    position: absolute;
                    right: 22px;
                    top: 45px;
                }
                .hidden-lable {
