                            display: block;
                            transition: all 0.3s ease;
                            &:hover {
                                background: rgba(0, 42, 58, 0.05);
                            }
                            &::after {
                                content: "";
                                border: 1px solid transparent;
                                background-color: transparent;
                                border-radius: 0px 10px 10px 0px;
                                height: 60px;
                                left: 0px;
                                position: absolute;
                                top: 50%;
                                transform: translateY(-50%);
                                width: 4px;
                                transition: all 0.3s ease;
                            }
                        }
                        .nav-link.active {
                            background: rgba(0, 42, 58, 0.05);
