                ul {
                    flex-wrap: wrap;
                }
            }
            .custom-row {
                flex-direction: column;
                gap: 20px;
                .myaccount-sidebar {
                    max-width: 100%;
                }
                .myaccount-sidebar-content {
                    .title-text {
                        h3 {
                            font-size: 16px;
                        }
                    }
                }
            }
        }
    }
    // employee-dashboard css
    .employee-dashboard {
        .sidebar-content {
            .dashboard-content {
                .dashboard-boxs {
                    .card-box {
                        max-width: 100%;
                        padding: 20px;

                        .card-content {
                            h2 {
                                font-size: 18px;
                            }
                        }
                    }
                }
                .job-status-box {
                    padding: 20px 20px 30px 20px;
                    .title-text {
                        .week-dropdown {
                            width: 90px;
                        }
                    }
                    .job-status-content {
                        margin-top: 30px;

                        .chart {
                            width: 220px;
                            height: 220px;
                            .chart-inside-text {
                                width: 125px;
                                height: 125px;
                                h3 {
                                    font-size: 22px;
                                }
                            }
                        }
                        .chart-content {
                            padding: 15px;
                            ul {
                                li {
                                    padding-left: 16px;
                                    margin-top: 15px;

                                    p {
                                        font-size: 14px;
                                    }
                                    &::before {
                                        width: 8px;
                                        height: 8px;
                                    }
                                    b {
                                        font-size: 14px;
                                    }
                                }
                            }
                        }
                    }
                }
            }
            .common-job-detils-box {
                .title-box {
                    flex-wrap: wrap;
                    row-gap: 15px;
                    padding: 20px 10px;
                    .title-text {
                        h3 {
                            font-size: 16px;
                        }
                    }
                    .new-opening-btn {
                        padding: 0 10px !important;
                    }
                }
            }
        }
    }
    // employee footer css
    .emp-footer {
        .emp-footer-content {
            .copyright-text {
                p {
                    font-size: 13px;
                    line-height: 18px;
                    text-align: center;
                }
            }
            .terms-links {
                ul {
                    li {
                        span {
                            font-size: 13px;
                        }
                    }
                }
            }
        }
    }
    .noresult p {
        font-size: 18px;
    }
}
.admin-policy {
    .title-text {
        display: flex;
        justify-content: space-between;
        align-items: center;
        .save-btn {
            margin-top: 0 !important;
        }
    }
}
