// Nemi Stylig css
// Candidate Page Css
.topspace {
    padding-top: 82px;
}

// header-area
.header-area {
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    background-color: #fff;
    z-index: 99;

    .navbar {
        padding: 16px 0px;
    }

    .admin-logo {
        max-width: 50px;
    }

    .logo-text {
        margin-left: 20px;
        transition: all .5s ease;
        max-width: 190px;
    }

    .navbar-collapse {
        .navbar-nav {
            .nav-link {
                font-style: normal;
                font-weight: 500;
                font-size: 16px;
                line-height: 19px;
                margin: 0px 24px;
                text-transform: capitalize;
                color: $darkgrey;
                position: relative;
                display: inline-block;
                color: $grey;

                &:after {
                    content: "";
                    position: absolute;
                    left: 0;
                    right: 0;
                    bottom: 0;
                    background-color: $black;
                    width: 13px;
                    height: 2px;
                    text-align: center;
                    margin: 0 auto;
                    background-color: transparent;
                }

                &:hover:after {
                    content: "";
                    background-color: $black;
                }
            }
        }
    }

    .signin-box {
        .login {
            margin-left: 19px;
            background-color: transparent;
            color: $primary;
            width: 163px;

            &.employee {
                width: 185px;
            }

            &:hover {
                background-color: $primary;
                color: $white;
            }
        }
    }

    // Target css
    .navbar-collapse .navbar-nav .nav-link.active::after {
        content: "";
        position: absolute;
        left: 0;
        right: 0;
        bottom: 0;
        background-color: $black;
        width: 13px;
        height: 2px;
        text-align: center;
        margin: 0 auto;
    }
}

// footer
.footer-area {
    background-color: black;

    .footer-top {
        padding: 111px 0px 84px;
    }

    .first-part {
        .footer-logo {
            margin-bottom: 43px;
        }

        p {
            margin-bottom: 40px;
            color: $darkgrey;
        }

        .connect-social {
            h5 {
                color: white;
                margin-bottom: 20px;
                line-height: 21.78px;
                font-weight: 500;
            }

            ul {
                li {
                    display: inline-flex;

                    .icon {
                        background: rgba(255, 255, 255, 0.15);
                        border-radius: 5px;
                        width: 35px;
                        height: 35px;
                        display: inline-flex;
                        justify-content: center;
                        align-items: center;
                        margin-right: 10px;
                        cursor: pointer;
                        transition: all 0.3s ease-in-out;
                        border: 1px solid transparent;

                        &:hover {
                            background-color: black;
                            border: 1px solid $grey;
                        }
                    }
                }
            }
        }
    }

    .second-part {
        .link-box {
            .nav-link {
                color: $darkgrey;
                font-family: Inter;
                font-size: 18px;
                font-weight: 500;
                line-height: 22px;
                letter-spacing: 0em;
                text-align: left;
                margin-bottom: 20px;
                color: $darkgrey;

                &.active,
                &:hover {
                    color: $white;
                }

                .contactlink-box {
                    display: flex;
                    justify-content: flex-start;
                    align-items: baseline;
                    margin-bottom: 17px;

                    .icon {
                        margin-right: 18px;
                        display: block;
                    }

                    span {
                        color: $darkgrey;
                        font-size: 18px;

                        &:hover {
                            color: $white;
                        }
                    }
                }
            }
        }

        &.partion {
            h5 {
                color: $white;
                margin-bottom: 43px;
            }
        }
    }

    .footer-bottom {
        padding: 31px 0px;
        border-top: 1px solid #525252;

        .copy-right {
            p {
                text-align: center;
                color: $white;
            }
        }
    }

    // Target css
    .second-part.second {
        padding-left: 148px;
    }
}

// hero-banner
.hero-banner {
    .banner-contentbox {
        position: absolute;
        top: 360px;
        left: 0;
        right: 0;
        transform: translate(0%, -50%);
        width: 100%;
        max-width: 1470px;
        margin: 0 auto;
        padding: 0px 15px;
        text-align: center;
    }

    .filter-box {
        display: flex;
        justify-content: flex-start;
        align-items: center;
        background-color: white;
        padding: 10px 0px;

        .dropdown {
            padding: 0px 0px;
            width: 100%;
            border-left: 1px solid #eee;

            &:last-child {
                border-left: none;
            }

            .search-btn {
                width: 100%;
                text-align: center;

                .common-btn {
                    background-color: $primary;
                    color: $white;
                    transition: all 0.3s ease;
                    border: 1px solid $primary;
                    width: 191px;

                    &:hover {
                        background-color: transparent;
                        color: $primary;
                        border: 1px solid $primary;
                    }
                }
            }

            .dropdown-toggle {
                background-color: transparent !important;
                border: none;
                border-radius: 0px;
                color: $black;
                height: 40px;
                font-size: 16px;
                display: flex;
                justify-content: flex-start;
                align-items: center;
                padding-right: 40px;
                font-weight: 500;
                width: 100%;

                &::after {
                    position: absolute;
                    right: 17px;
                    content: url("../images/downarrow.png");
                    border: none;
                    line-height: 0;
                }

                span {
                    display: block;
                    margin-right: 10px;
                    line-height: 1;

                    svg {
                        width: 19px;
                        height: 19px;
                    }
                }
            }

            .dropdown-menu {
                width: 100%;
                position: absolute !important;
                top: 0px !important;
                border-radius: 0px;
                border: none;
                background: #ffffff;
                box-shadow: 6px 14px 43px rgba(0, 0, 0, 0.1);

                .dropdown-item {
                    padding: 10px 20px;
                    transition: all 0.3s ease-in-out;

                    &:hover {
                        background-color: $primary;
                        color: $white;
                    }
                }
            }
        }
    }

    .banner-content {
        margin-top: 62px;

        p {
            margin: 19px 0px 51px;
            text-align: center;
        }

        .join-us {
            width: 229px;
        }
    }

    .vector-image {
        position: absolute;
        bottom: -135px;
        left: -85px;
    }
}

