}

// Newest Job Listing
.jobsforyou-area {
    background-color: #f9f9f9;
    padding: 96px 0px;

    .jobswhole-box {
        margin-top: 67px;
    }

    .joblistwhole-box {
        max-height: 810px;
        overflow: auto;
        position: relative;

        .joblisting-box {
            padding: 30px 25px;
            background: #ffffff;
            border-radius: 5px;
            margin-bottom: 20px;
            box-shadow: none;
            transition: all 0.3s ease;
            border: 1px solid transparent;
            cursor: pointer;

            &:hover,
            &.active {
                background-color: #f1f4f1;
                border: 1px solid rgba(0, 42, 58, 0.65);
            }

            .job-box {
                display: flex;
                justify-content: space-between;
                align-items: flex-start;
                margin-bottom: 20px;

                .logo-titlebox {
                    display: flex;
                    justify-content: flex-start;
                    align-items: flex-start;

                    .clogo {
                        width: 70px;
                        height: 70px;
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        margin-right: 20px;
                        background: #fafafa;
                        border: 1px solid #f2f2f2;
                        border-radius: 5px;
                    }

                    .ctitle {
                        h5 {
                            margin-bottom: 5px;
                        }

                        span {
                            color: $grey;
                            font-weight: 500;
                        }
                    }
                }

                .days {
                    span {
                        font-size: 14px;
                        color: $grey;
                    }
                }
            }

            .labels-box {
                li {
                    display: inline-flex;

                    .labels {
                        margin-right: 10px;
                        margin-bottom: 10px;
                        padding: 0px 7px;
                        background-color: #f2f4f5;
                    }
                }
            }
        }
    }

    .jobview-box {
        border-radius: 5px;
        max-height: 810px;
        overflow: auto;
        background-color: white;

        .recentpost {
            display: flex;
            justify-content: space-between;
            align-items: center;
            padding: 13px 25px;
            border-bottom: 1px solid #dedede;
            background-color: white;

            .viewfull {
                text-decoration: underline;
                cursor: pointer;

                &:hover {
                    color: $grey;
                }
            }
        }

        .joblisting-box {
            padding: 19px 30px;
            background-color: $white;

            .job-box {
                display: flex;
                justify-content: space-between;
                align-items: flex-start;

                .logo-titlebox {
                    display: flex;

                    .clogo {
                        width: 60px;
                        height: 60px;
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        margin-right: 20px;
                    }

                    .ctitle {
                        h5 {
                            margin-bottom: 5px;
                        }

                        span {
                            color: $grey;
                        }
                    }
                }

                .apply-box {
                    display: flex;
                    justify-content: flex-end;
                    align-items: center;

                    span {
                        .dropdown {
                            margin-left: 10px;

                            .dropdown-toggle {
                                width: 45px;
                                height: 45px;
                                display: flex;
                                justify-content: center;
                                align-items: center;
                                background: #f1f4f1;
                                border-radius: 7px;
                                border: 1px solid $white;

                                &:after {
                                    display: contents;
                                }
                            }
                        }

                        .common-btn {
                            width: 146px;
                            height: 45px;
                        }

                        &.days {
                            margin-right: 14px;
                            color: $grey;
                            white-space: nowrap;
                        }
                    }
                }
            }

            .label-shareonbox {
                display: flex;
                justify-content: space-between;
                align-items: center;
                margin-top: 27px;

                .labels-box {
                    width: 72%;

                    li {
                        display: inline-flex;

                        .labels {
                            margin-right: 10px;
                            color: $grey;
                            background: rgba(0, 42, 58, 0.05);
                            border-radius: 4px;
                            cursor: pointer;
                        }
                    }
                }
            }

            .connect-social {
                h5 {
                    font-weight: 500;
                    font-size: 14px;
                    text-align: end;
                    margin-bottom: 6px;
                }

                .icon {
                    width: 35px;
                    height: 35px;
                    display: inline-flex;
                    justify-content: center;
                    align-items: center;
                    background: #f2f4f5;
                    border-radius: 5.71429px;
                    margin-left: 10px;
                    border: 1px solid transparent;
                    cursor: pointer;
                    transition: all 0.3s ease;

                    &:hover {
                        background-color: white;
                        border: 1px solid $primary;
                    }

                    svg {
                        filter: invert(1);
                    }
                }
            }
        }

        .tabmain-box {
            background-color: white;
            padding: 19px 30px;

            .job-tabs {
                margin-top: 10px;

                .nav-item {
                    display: flex;

                    .nav-link {
                        background-color: transparent;
                        border: none;
                        width: 219px;
                        color: $primary;
                        font-weight: 600;
                        font-size: 16px;
                        line-height: 26px;
                        color: $grey;
                        border-bottom: 1px solid transparent;

                        &.active {
                            color: $primary;
                            border-bottom: 1px solid $primary;
                        }
                    }
                }
            }

            .tab-content {
                height: 500px;
                overflow: auto;

                .tab-pane {
                    p {
                        font-size: 14px;
                        color: black;
                    }
                }

                .skillbox {
                    li {
                        display: inline-flex;
                    }
                }

                .overview-content {
                    .cabox {
                        margin-bottom: 16px;
                    }

                    .skillbox {
                        .labels {
                            background: rgba(0, 42, 58, 0.05);
                            border-radius: 4px;
                            margin-right: 4px;
                        }
                    }
                }
            }
        }
    }

    .viewlisting {
        display: flex;
        justify-content: flex-end;
        align-items: center;

        .common-btn {
            width: 207px;
            height: 55px;
            margin: 0px -24px;
        }
    }

    // target css
    .joblistwhole-box .joblisting-box:hover li .labels {
        background-color: white;
    }

    .joblistwhole-box .joblisting-box.active li .labels {
        background-color: white;
    }
}

