            margin-bottom: 35px;
            font-size: 30px;
            font-weight: 700;
        }
    }

    .slick-slider {
        .slick-list {
            .slick-track {
                margin: 0px -22px;

                .similarjob-box {
                    border: 1px solid rgba(176, 176, 176, 0.2);
                    box-shadow: 7px 31px 81px rgba(0, 0, 0, 0.05);
                    border-radius: 5px;
                    padding: 50px 30px;
                    box-shadow: none;
                    margin: 0px 22px;

                    .companymain-box {
                        display: flex;
                        justify-content: space-between;
                        align-items: baseline;
                        margin-bottom: 24px;

                        .company-box {
                            display: flex;
                            justify-content: flex-start;
                            align-items: flex-start;

                            .company-logo {
                                width: 69px;
                                height: 69px;
                                background: #fafafa;
                                border: 1px solid #f2f2f2;
                                border-radius: 5px;
                                display: flex;
                                justify-content: center;
                                align-items: center;
                                margin-right: 20px;
                            }

                            .company-title {
                                h5 {
                                    margin-bottom: 5px;
                                }

                                span {
                                    color: $grey;
                                }
                            }
                        }

                        .days {
                            color: $grey;
                            font-size: 12px;
                            white-space: nowrap;
                        }
                    }

                    .labels-box {
                        li {
                            display: inline-flex;

                            .labels {
                                margin-right: 10px;
                                background: rgba(0, 42, 58, 0.05);
                                border-radius: 4px;
                            }
                        }
                    }

                    p {
                        font-size: 14px;
                        margin-bottom: 27px;
                    }

                    .apply-now {
                        .common-btn {
                            display: block;
                            width: 100%;
                            background: rgba(0, 42, 58, 0.2);
                            border-radius: 5px;
                            color: $primary;
                            border: 1px solid transparent;

                            &:hover {
                                background-color: transparent;
                                border: 1px solid $primary;
                            }
                        }
                    }
                }
            }
        }

        .slick-arrow {
            z-index: 200;
            background: #f1f4f1;
            border: 1px solid #e1e1e1;
            border-radius: 7px;
            width: 50px;
            height: 50px;
            display: flex;
            justify-content: flex-start;
            align-items: center;
        }

        .slick-arrow.slick-next {
            right: 18px;
        }

        .slick-arrow.slick-prev {
            left: -35px;
        }

        .slick-prev:before {
            content: url("../images/leftarrow.svg");
        }

        .slick-next:before {
            content: url("../images/rightarrow.svg");
        }
    }
}

