// contactus page
.contactus-page {
    .contactus-banner {
        text-align: center;
        background-color: #f1f4f1;
        padding: 32px 0px 114px;
    }

    .contactmain-box {
        position: relative;
        top: -58px;

        .contactus-box {
            height: 292px;
            text-align: center;
            display: flex;
            justify-content: center;
            align-items: center;
            flex-direction: column;
            background: #ffffff;
            border: 1px solid rgba(176, 176, 176, 0.2);
            box-shadow: 7px 31px 81px rgba(0, 0, 0, 0.05);
            border-radius: 5px;
            cursor: pointer;

            &:hover {
                background-color: #f1f4f1;
            }

            .icon {
                width: 72px;
                height: 72px;
                display: flex;
                justify-content: center;
                align-items: center;
                background: #f1f4f1;
                border-radius: 5px;
                margin: 0 auto;
                margin-bottom: 20px;
            }

            .content {
                h5 {
                    font-weight: 600;
                    font-size: 22px;
                    margin-bottom: 15px;
                    text-align: center;
                }

