// loader
.loaderbox {
    height: 100vh;

    .spin-loader {
        position: absolute;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        background-color: #00000040;
    }

    .loader {
        color: $primary;
        font-size: 15px;
        margin: 100px auto;
        width: 1em;
        height: 1em;
        border-radius: 50%;
