            //     width: 100%;
            //     margin-left: auto;
            //     .contentwhole-box {
            //         background-color: white;
            //     }
            // }
        }

        .collapse-icon {
            position: absolute;
            top: 10px;
            z-index: 100;
            background-color: transparent;
            border: none;
            left: 10px;
            transform: rotate(180deg);
            transition: all 0.3s ease;
            padding: 0px 0px !important;

            svg {
                width: auto;
                height: 35px;

                path {
                    fill: $primary;
                }
            }
        }
    }

    .navbar {
        li {
            width: 100%;
            a {
                background-color: transparent;
                border-radius: 5px;
                color: $black;
                width: auto;
                height: 55px;
                display: flex;
                justify-content: flex-start;
                align-items: center;
                margin-bottom: 7px;
                white-space: nowrap;
                margin: 4px;
                padding: 0px 11px;
                transition: all 0.5s ease-in-out;
                font-size: 16px;
                span {
                    margin-right: 21px;

                    svg {
                        width: 22px;
                        height: 22px;

                        path {
                            fill: $black;
                        }
                    }
                }
                .link-text{
                    margin: 0;
                }
                &.faq{
                    &::after {
                        content: '';
                        background: url("../images/black-down-arrow.svg");
                        border: none;
                        margin: 0px;
                        padding: 0px;
                        position: absolute;
                        right: 18px;
                        z-index: 2;
                        width: 12px;
                        height: 20px;
                        background-repeat: no-repeat !important;
                        background-position: right;
                        background-size: contain;
                        transition: 0.3s ease-in-out;
                    }
                    &.active{
                        &::after{
                            background: url("../images/white-down-arrow.svg");
                            background-repeat: no-repeat !important;
                            background-position: right;
                            background-size: contain;
                            transform: rotate(180deg);
                        }
                        
                    }
                    
                }
            }
            &.droparrow{
                transition: 0.4s ease-in-out;
                a{
                    &.faq{
                        &::after{
                            transform: rotate(180deg) !important;
                        }  
                    }  
                }        
            }
            &.arrow{
                transition: 0.4s ease-in-out;
                a{
                    &.faq{
                        &::after{
                            transform: rotate(0deg) !important;
                        }  
                    }  
                }  
            }
        }

