// Admin-dashboad css
.admin-counter {
    .card-box {
        background: #ffffff;
        box-shadow: 7px 30px 81px rgb(0 0 0 / 2%);
        border-radius: 5px;
        padding: 25px 30px 23px 25px;
        position: relative;
        overflow: hidden;
        margin-bottom: 20px;

        .header-text {
            display: flex;
            align-items: center;
            column-gap: 20px;

            .card-image {
                min-width: 59px;
                min-height: 59px;
                background: #f2ebfb;
                border-radius: 46px;
                display: flex;
                justify-content: center;
                align-items: center;
                svg{
                    width: 28px;
                    stroke: #814b99;
                    stroke-width: 0.2px;
                }
                &.blue {
                    background-color: #eef9ff;
                }

                &.lightyellow {
                    background-color: #fef3ef;
                }

                &.lightpink {
                    background-color: #ffeff2;
                }
                &.light-green{
                    background-color: #F1FFEE;
                }
                &.light-purple{
                    background-color: #ecf1ff;
                }
                &.off-white{
                    background-color: #FFFBEE;
                }
            }

            h3 {
                font-size: 16px;
            }
        }

        .card-content {
            display: flex;
    justify-content: center;
    align-items: center;
    height: inherit;

