    }
    // hamburger menu
    .navbar-collapse {
        background-color: #fff;
        height: 100vh !important;
        left: -100%;
        width: 100%;
        padding: 100px 15px;
        position: absolute;
        top: 0;
        transition: all 0.3s ease-in-out;
        z-index: -1;
        overflow-y: scroll;
        &::-webkit-scrollbar{
            width: 0;
        }
    }

    .navbar-collapse.show {
       left: 0;
       transition: all 0.3s ease-in-out;
    }

    .header-area .navbar-collapse .navbar-nav .nav-link {
        margin: 5px 0px;
        width: 100%;

        &.active {
            color: white;
