.multi-space {
    position: relative;
    // transform: translate(0%, -50%);
    .multishare-box {
        .action-btn {
            width: 35px;
            height: 35px;
            background-color: #814b99;
            border: none;
            border-radius: 50%;
            display: flex;
            justify-content: center;
            align-items: center;
            padding-left: 35px;
            font-size: 25px;
            cursor: pointer;
            padding: 0px;

            &:hover {
                background-color: #814b99;
            }
            &:hover .social-icons {
                opacity: 1;
                transform: translate(0px, -45px);
                visibility: visible;
                display: block;
            }
            svg {
                fill: white;
                width: 16px;
                height: 16px;
            }
        }

        .social-icons {
            list-style-type: none;
            
            opacity: 0;
            position: absolute;
            margin: 0px;
            left: 0;
            top: 6px;
            right: 0;
            border-radius: 30px;
            background-color: #f8f9fa;
            color: white;
            border: 0.5px solid #814b99;
            z-index: 10;
            line-height: 1;
            padding: 5px;
            transition: all 0.3s ease;
            transform: translate(0px, 0px);
            visibility: hidden;
            display: block;
            transition-delay: 0.1s;
           width: max-content;
            li {
                display: inline-flex;
                border-right: 1px solid #814b99;
                line-height: 0;
                &:last-child {
                    border: none;
                }
                a {
                    vertical-align: middle;
                    position: relative;
                    top: -3px;
                }
                span {
                    margin: 0px 10px;
                    color: white;
                    text-decoration: none;
                    svg {
                        width: 16px;
                        height: 16px;
                        path {
                            fill: #814b99 !important;
                        }
                    }
                }
            }

            &.mystyle {
                opacity: 1;
                transition: all 0.3s ease;
                transform: translate(0px, -50px);
                cursor: pointer;
            }

        }
    }
}


