html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
sub,
sup,
tt,
var,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
    border: 0;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
    display: block;
}

ol,
ul {
    list-style: none;
}
blockquote,
q {
    quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
    content: "";
    content: none;
}
table {
    border-collapse: collapse;
    border-spacing: 0;
}
ul,
ol {
    padding: 0px 0px;
}
.ck.ck-editor__main {
    ul,
    ol {
        padding-left: 30px;
        // list-style: auto;
        li {
            line-height: 30px;
        }
    }
   
}
.ck.ck-editor__main>.ck-editor__editable{
    min-height: 300px;
}
.modal-dialog .ck.ck-editor__main > .ck-editor__editable{
    min-height: 200px;
}
.modal-dialog{
    .profile-image{
        display: inline-block;
        position: relative;
        margin-bottom: 15px;
        .selectimg{
            position: absolute;
            top: -8px;
            right: -8px;
            cursor: pointer;
        }
    }
}

.jobdesbox ol {
    padding-left: 30px;
    list-style-type: auto;
}
.page-content {
    ul,
    ol {
        // list-style-type: revert;
        li {
            line-height: 30px;
            margin-bottom: 20px;
        }
    }
}
