// Variables
$primary: #814b99;
$black: #000000;
$grey: #666666;
$darkgrey: #a6a6a6;
$white: #ffffff;
$lightgreen: #f1f4f1;
$grey94: #949494;

// Styling Nemi Sanghvi
body {
    line-height: 1;
    font-size: 16px;
    font-family: "Inter", sans-serif;
    background-color: #f5f5f5;
}
.container {
    width: 100% !important;
    max-width: 1470px !important;
    padding: 0px 15px !important;
    margin: 0 auto;
}
h1 {
    font-family: "Inter";
    font-size: 50px;
    font-weight: 700;
    line-height: 70px;
    text-align: center;
    margin: 0px;
}
h2 {
    font-family: "Inter";
    font-size: 40px;
    font-weight: 700;
    line-height: 55px;
    margin: 0px;
}
h3 {
    font-family: "Inter";
    font-size: 30px;
    font-weight: 700;
    line-height: 36px;
    margin: 0px;
}
h4 {
    font-family: "Inter";
    font-size: 18px;
    font-weight: 500;
    line-height: 73px;
    color: $grey94;
    margin: 0px;
}
h5 {
    font-family: "Inter";
    font-size: 18px;
    font-weight: 600;
    line-height: 26px;
    letter-spacing: 0px;
    text-align: left;
    margin: 0px;
}
h6 {
    font-family: "Inter";
    font-size: 14px;
    font-weight: 500;
    line-height: 17px;
    letter-spacing: 0em;
    text-align: left;
    margin: 0px;
}
p {
    font-family: "Inter";
    font-size: 18px;
    font-weight: 500;
    line-height: 32px;
    text-align: left;
    color: #666666;
    margin: 0px 0px;
}
