    text-decoration: none;
    &:hover {
        color: inherit;
    }
}
.error-message {
    color: red;
    font-size: 12px;
    line-height: 12px;
    position: absolute;
    display: block;
    padding-top: 2px;
}

b {
    color: black;
    font-weight: 300;
    font-size: 14px;
    font-weight: 600;
}
b {
    color: black;
    font-weight: 300;
    font-size: 14px;
    font-weight: 600;
}
.common-btn {
    font-family: "Inter";
    font-size: 16px;
    font-weight: 600;
    line-height: 19px;
    letter-spacing: 0em;
    text-align: center;
    background-color: $primary;
    color: white;
    padding: 0px 0px;
    width: 163px;
    height: 50px;
    border: 1px solid $primary;
    border-radius: 5px;
    transition: all 0.3s ease;
    &:hover {
        background-color: transparent;
        color: $primary;
        border: 1px solid $primary;
    }
    
    &:first-child:active {
        background-color: $primary;
        color: white;
        border: 1px solid $primary;
    }
}
.login{
    .comman-auth-btn{
        &:active{
            background-color: $primary; 
            border-color: $primary;
            color: $white;
        }
    }
}
.input-wrapper {
    .lcontrol {
        font-family: "Inter";
        font-size: 14px;
        font-weight: 500;
        line-height: 26px;
        letter-spacing: 0px;
        text-align: left;
        color: #666666;
    }
    .icontrol {
        width: 100%;
        height: 50px;
        border-radius: 5px;
        border: 1px solid #f5f5f5;
        background-color: #f8f8f8;
        border-radius: 5px;
        outline: none;
        &::placeholder {
            font-family: "Inter";
            font-style: normal;
            font-weight: 500;
            font-size: 16px;
            line-height: 26px;
            color: #000000;
            background-color: #f8f8f8;
        }
        &:focus {
            box-shadow: none;
            border: none;
            border: 1px solid #f5f5f5;
            background: #f8f8f8;
            z-index: 1;
        }
    }
    .itype {
        background-color: white;
        border: none;
        background-color: transparent;
        border: none;
        width: 100%;
        color: rgba(0, 0, 0, 0.6);
        padding: 0px 0px;
        &::placeholder {
            background: transparent;
            border: none;
            font-size: 16px;
            line-height: 26px;
            color: rgba(0, 0, 0, 0.6);
        }
        &:focus {
            background: transparent;
            border: none;
        }
    }
    .tcontrol {
        height: 134px;
        width: 100%;
        border-radius: 5px;
        background-color: #f5f5f5;
        border-radius: 5px;
        border: 1px solid #f5f5f5;
        color: black;
        font-family: "Inter";
        font-style: normal;
        font-weight: 500;
        font-size: 16px;
        line-height: 26px;
        resize: none;
        &::placeholder {
            font-family: "Inter";
            font-style: normal;
            font-weight: 400;
            font-size: 16px;
            line-height: 26px;
            color: #000000;
            background: #f8f8f8;
        }
        &:focus {
            box-shadow: none;
            border: 1px solid #f5f5f5;
            background: #f8f8f8;
        }
    }
    .input-group-text {
        background: #f8f8f8;
        border: 1px solid #f5f5f5;
        border-radius: 5px;
        position: absolute;
        right: 0;
        height: 50px;
        z-index: 2;
        cursor: pointer;
    }
}

.dropdown {
    .dropdown-toggle {
        color: black;
        display: flex;
        justify-content: space-between;
        align-items: center;
        border: 1px solid transparent;
        background-color: #f8f8f8 !important;
        border: 1px solid transparent !important;
        &::after {
            content: url("../images/down-arrow.svg");
            border: none;
            margin: 0px;
            padding: 0px;
            position: absolute;
            right: 18px;
            z-index: 2;
            transition: 0.3s ease-in-out;
        }
        &:hover {
            border: 1px solid transparent;
            background-color: #f8f8f8;
        }
        &.show{
            &::after{
              transform: rotate(180deg);  
            }   
        }
    }
    .dropdown-menu {
        width: 100%;
        padding: 0px;
        min-width: 100%;
        .dropdown-item {
            padding: 10px 15px;
            font-weight: 500;
            &:hover {
                background-color: $primary;
                color: white;
            }
            &.active{
                background-color: $primary; 
                color: white;
            }
        }
    }
    .icontrol {
        border: none;
        &:focus {
            box-shadow: none;
        }
    }
}
.dropdown-menu {
    width: 100%;
    padding: 0px;
    min-height: auto;
    overflow-x: hidden;
    height: auto;
    max-height: 200px;
    .dropdown-item {
        padding: 10px 15px;
        font-weight: 500;
        &:hover {
            background-color: $primary;
            color: white;
        }
    }
}

.radio-btn {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    .form-check {
        display: flex;
        justify-content: center;
        align-items: center;
        .form-check-input {
            margin-right: 10px;
            vertical-align: middle;
            background-color: $primary;
            border: 1px solid $primary;
        }
    }
}
.addcontrol {
    background: #f8f8f8;
    border-radius: 5px;
    border: 0.5px solid rgba(0, 0, 0, 0.5);
    padding: 0px 20px;
    z-index: 1;
    border-left: 0.5px solid rgb(0 0 0 / 9%);
    border-top: 0;
    border-bottom: 0;
    border-right: 0;
}
.labels {
    background-color: #f2f4f5;
    border-radius: 4px;
    border: 1px solid #eee;
    display: inline-flex;
    height: 30px;
    text-align: center;
    justify-content: center;
    align-items: center;
    padding: 0px 10px;
    font-family: Inter;
    font-size: 14px;
    font-weight: 500;
    line-height: 26px;
    letter-spacing: 0px;
    text-align: left;
    margin-bottom: 10px;
    svg {
        margin-right: 9px;
        cursor: pointer;
    }
}
.whitebox {
    background: #ffffff;
    box-shadow: 7px 31px 81px rgba(0, 0, 0, 0.1);
    border-radius: 5px;
}
.social-icons {
    li {
        display: inline-flex;
        .icon {
            width: 35px;
            height: 35px;
            display: inline-flex;
            justify-content: center;
            align-items: center;
            background: #f2f4f5;
            border-radius: 5.71429px;
            border: 1px solid transparent;
            transition: all 0.3s ease;
            cursor: pointer;
            &:hover {
                background-color: white;
                border: 1px solid $primary;
            }
        }
    }
}
.contact-form .input-wrapper {
    margin-bottom: 34px !important;
}
// pagination
.pagination-items {
    background-color: transparent;
    border: none;
    display: flex;
    justify-content: center;
    align-items: baseline;
    margin-bottom: 0;
    .page-item {
        border: none;
        .page-link {
            background-color: transparent;
            border-radius: 4px;
            border: none;
            color: $black;
            margin: 0px 4px;
            line-height: 1;
            width: 36px;
            height: 36px;
            display: flex;
            justify-content: center;
            align-items: center;
            transition: all 0.3s ease;
            display: initial;
            &:hover {
                background-color: #814b99;
                color: white;
            }
            &:focus{
                box-shadow: none;
            }
        }
    }
    .next .page-link span {
        display: block;
    }
    .page-item.active .page-link {
        background-color: #814b99;
        color: white;
    }
    .next.page-item span.visually-hidden,
    .previous.page-item span.visually-hidden {
        display: contents;
    }
    .page-item .page-link > span {
        display: none;
    }
    .pagination-dot .page-link > span {
        display: block;
    }
    .pagination-dot {
        pointer-events: none;
        .page-link {
            display: inline-flex;
        }
    }
}
